import { Component, OnInit } from '@angular/core';
import { HeroService } from "../hero.service";

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  public contact = {
    id: null,
    name: "",
    address: "",
    dateStamp: new Date(),
    email: "",
    phone: "",
    message: "",
    phoneBackup: Number
  }

  constructor(public postsService: HeroService) { }

  ngOnInit(): void {
  }

  postContactUs() {
    this.postsService.addNewContact(this.contact).subscribe(data => {
      alert("Thank you for the feedback");
    },
      error => alert("Error: Request can't send at this time")
    )
  }

}
